import WordSpelling from '../../../../../component/wordSpelling.vue'
import ChineseWordSelection from '../../../../../component/chineseWordSelection.vue'
import ListeningWordSelection from '../../../../../component/listeningWordSelection.vue'
import EnglishSelection from '../../../../../component/englishSelection.vue'
export default {
	data() {
		return {
			percentage: '',
			wordIdList: [],
			questionList: [],
			index: 0,
			timer: null,
			Hours: 0,
			Minute: 0,
			Second: 0,
			Hourst: 0,
			Minutet: 0,
			Secondt: 0,
			time: "",
			optionsList: [],
			answer: "",
			wordIdListStr: "",
			subject: [],
			cIndex: '',
			answerValue: {},
			answerList: [],

			sucess: false,
			error: false

		}
	},
	components: {
		WordSpelling,
		ChineseWordSelection,
		ListeningWordSelection,
		EnglishSelection
	},
	computed: {

	},
	methods: {
		// 返回
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 获取题目
		getQuestion(wordId) {
			// console.log(wordId)
			this.$minApi.getQuestion({
				wordId: wordId
			}).then(data => {
				this.questionList = data.data
				if (data.data.questionType == 2) {
					let title = this.questionList.question_stem.replace(/ /g, "&")
					this.subject = title.split('')
					this.answer = this.questionList.question_answer
				} else {
					this.optionsList = this.questionList.question_option.split("丶")
					this.answer = this.questionList.question_answer
					// console.log(this.questionList)
				}

			})
		},

		// 下一题
		nextQuestion() {
			if (this.index <= this.wordIdList.length - 2) {
				this.index++
				this.percentage = ((this.index + 1) / this.wordIdList.length) * 100
				this.getQuestion(this.wordIdList[this.index])
			} else {
				clearInterval(this.timer)
				uni.navigateTo({
					url: "passedTask?wordCount=" + this.wordIdList.length + "&time=" + this.time + "&wordId=" + this.questionList.word_id
				})
			}
		},
		// 单词拼写下一题
		nextSpell() {
			console.log(this.answer)
			let stringValue = this.questionList.question_stem.replace(/ /g, "&")
			var positions = new Array();
			var pos = stringValue.indexOf("&");
			this.answerList = []
			while (pos > -1) {
				positions.push(pos);
				pos = stringValue.indexOf("&", pos + 1);
			}
			positions.forEach(data => {
				console.log(data)
				this.answerList.push({
					["answer" + data]: this.answer.substr(data, 1)
				})
			})
			console.log(this.answerList[0].answer3)
			console.log(this.answerList.answer3)
			console.log(this.answerValue)
			console.log(this.answerValue.answer3)
		},
		// 选择选项
		check(index) {
			this.cIndex = index
			console.log(this.cIndex)
			console.log(index)
			console.log(this.answer)
			if (index == this.answer) {
				console.log("r")
				setTimeout(() => {
					this.nextQuestion();
				}, 1000)
			} else {
				console.log("f")
				setTimeout(() => {
					uni.navigateTo({
						url: "wordDetails?wordId=" + this.questionList.word_id + "&doneCount=" + this.index + "&wordIdListStr=" +
							this.wordIdListStr +
							"&time=" + this.time
					})
				}, 1000)
			}
			// 个人任务答题
			this.$minApi.completeTask({
				taskId: getApp().globalData.taskId,
				wordId: this.questionList.word_id
			}).then(data => {
				console.log(data)
			})
		},

		// 计时器、
		start() {
			this.timer = setInterval(data => {
				this.Second++;
				if (this.Second > 59) {
					this.Second = 0;
					this.Minute++;
					if (this.Minute > 59) {
						this.Minute = 0;
						this.Hours++;
					}
				}
				if (this.Second < 10) {
					this.Secondt = "0" + this.Second;
				} else {
					this.Secondt = this.Second;
				}
				if (this.Minute < 10) {
					this.Minutet = "0" + this.Minute;
				} else {
					this.Minutet = this.Minute;
				}
				if (this.Hours < 10) {
					this.Hourst = "0" + this.Hours;
				} else {
					this.Hourst = this.Hours;
				}
				this.time = this.Hourst + ":" + this.Minutet + ":" + this.Secondt
			}, 1000)
		},

		// 停止计时
		stop() {
			clearInterval(this.timer)
		}
	},
	onLoad(options) {
		this.wordIdListStr = options.wordIdList
		this.wordIdList = options.wordIdList.split(",")
		this.percentage = ((this.index + 1) / this.wordIdList.length) * 100
		this.start()
		if (options.index) {
			this.index = parseInt(options.index)
			this.percentage = ((this.index + 1) / this.wordIdList.length) * 100
		}
		if (options.time) {
			this.time = options.time
			this.Hours = parseInt(this.time.split(":")[0])
			this.Minute = parseInt(this.time.split(":")[1])
			this.Second = parseInt(this.time.split(":")[2])
		}
		this.getQuestion(this.wordIdList[this.index])
		console.log(getCurrentPages())
	}
}
